/* src/components/CoursesList.css */
.benifits-list {
    padding: 20px;
    text-align: center;
    padding-top: 40px;
    font-family:"Poppins", sans-serif;
  }
  
  .benifits-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 5px;
  }
  
  .benifits-card {
    width: 300px;
    border-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    background: #fff;
    margin: 10px;
  }
  
  .benifits-img {
    width: 100%;
    height: 190px;
    object-fit: cover;
  }
  
  .course-info {
    padding: 15px;
  }

  span{
    color: #4b2e77;
  }